$time: .5s;
$easeBounce: cubic-bezier(0.23, 1, 0.32, 1) ;
$easeOutQuart: cubic-bezier(0.165, 0.840, 0.440, 1.000);

/*
 * Transitions from pages (left/right translate)
 */
.page-transition-enter {
  position: absolute;
  opacity: 0;
  will-change: transform, opacity;
  transform: translateX(100%);
}
.page-transition-enter.page-transition-enter-active {
  opacity: 1;
  transform: translateX(0px);
  transition: all $time $easeOutQuart;
}

.page-transition-leave {
  position: absolute;
  opacity: 1;
  will-change: transform, opacity;
}

.page-transition-leave.page-transition-leave-active {
  opacity: 0;
  transform: translateX(-100%);
  transition: all $time $easeOutQuart;
}

.page-transition-appear {
  position: absolute;
  opacity: 0;
}

.page-transition-appear.page-transition-appear-active {
  opacity: 1;
  transform: translateX(0px);
  transition: all $time $easeOutQuart;
}






/*
 * Transition for hexagram-preview
 * (translateY , from bottom hidden to bottom of screen)
 */
.hexagram-preview-enter {
  animation-duration: $time;
  animation-timing-function: $easeOutQuart;
  animation-fill-mode: both;
  opacity: 0;
}

.hexagram-preview-enter.hexagram-preview-enter-active {
  animation-name: slideFromBottom;
}

.hexagram-preview-leave {
  animation-duration: $time;
  animation-timing-function: $easeOutQuart;
  animation-fill-mode: both;
  opacity: 1;
}

.hexagram-preview-leave.hexagram-preview-leave-active {
  animation-name: slideToTop;
}

.hexagram-preview-appear {
  animation-duration: $time;
  animation-timing-function: $easeOutQuart;
  animation-fill-mode: both;
}
.hexagram-preview-appear.hexagram-preview-appear-active {
  animation-name: slideFromBottom;
}


